@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face{font-family:Circular;src:url(https://mikeyriver2.github.io/swarm-buble/static/media/CircularStd-Book.c1441dbf.otf)}.messagePreview{display:flex;width:100%;height:63px;margin:12px 0px;text-decoration:none}.messagePreview.hasUnread .messagePreview__message h3,.messagePreview.hasUnread .messagePreview__message p{font-weight:bold;color:#352956}.messagePreview__avatar{width:48px;height:48px}.messagePreview__message{margin-left:12px;max-width:226px;white-space:break-spaces}.messagePreview__message h3{margin:0px;font-weight:100;font-size:16px;color:#352956}.messagePreview__message p{margin:5px 0px;font-size:14px;color:#9A95AA;width:235px}.messagePreview__details{text-align:right;max-width:60px}.messagePreview__details p{margin:0px;font-size:12px;text-align:right;color:#9A95AA}.messagePreview__details .count{margin-top:10px}.userPreview{display:flex;align-items:center;justify-content:space-between;margin-top:10px;text-decoration:none}.userPreview .avatar{width:20px;height:20px;margin-right:8px}.userPreview__user{display:flex;align-items:center}.userPreview__user p{color:#9A95AA;margin:0px;font-size:14px}.userPreview__user p:first-of-type{margin-right:6px}.userPreview.hasUnread .userPreview__user p:first-of-type{font-weight:bold;color:#352956}.thread{width:calc(100vw - (360px + (32px * 2)));display:flex}.thread__contents{width:calc(100vw - (360px + (32px * 4)))}.thread .threadDetails{width:360px}.thread__header{border-bottom:2px #F3F1F6 solid;padding:16px 25px}.thread__header h2{margin:0px}.thread__contents{border-left:2px #F3F1F6 solid;border-right:2px #F3F1F6 solid}.thread__messages{padding:30px 25px;overflow:auto;display:flex;flex-direction:column}.thread__messages .message:first-child{margin:0px 0px 25px 0px;margin-top:auto !important}.thread__messages .message:not(:first-child){margin:25px 0px}.thread__divider{border-top:1px solid #F3F1F6;position:relative;margin:30px 0px;color:#BBB8C5}.thread__divider p{position:absolute;width:100%;text-align:center;top:-28px}.thread__divider p span{padding:9px;background:#F7F6FA;border:8px solid #FFFFFF;border-radius:23px;font-size:12px}.threadInput{text-align:center;padding:20px 0px}.threadInput input{width:90%;background:#F7F6FA;border-radius:24px;padding:14px 24px;border:none;outline:0px}.threadInput input:focus{box-shadow:inset 0px 0px 7px 0px #51515542}.message__contents{display:flex;align-items:flex-end}.message .avatar{width:32px;height:32px;margin-right:8px}.message__bubble{padding:16px 24px;max-width:345px;background:#756AF0;border-radius:16px 16px 16px 0px}.message__bubble p{margin:0px;color:white}.message__details{padding:6px 0px 0px 38px;color:#9A95AA;font-size:14px}.message.isOwn .avatar{display:none}.message.isOwn .message__bubble{padding:16px 24px;background:#F7F6FA;border-radius:16px 16px 0px 16px}.message.isOwn .message__bubble p{color:#352956}.message.isOwn .message__contents{width:100%;justify-content:flex-end}.message.isOwn .message__details{text-align:right}.avatar{border-radius:50%;width:48px;height:48px}.avatar img{max-width:100%;max-height:100%;border-radius:50%}.threadDetails{padding:32px;max-width:360px}.threadDetails__header{display:flex;align-items:center;flex-direction:column}.threadDetails .userPreview__user p:first-of-type{color:#352956}.swarm{display:flex;height:100vh;font-family:Circular}.swarm__landing{max-width:360px;width:360px;padding:25px 32px}.swarm__landing__inbox h4{font-size:12px;color:#9A95AA;text-transform:uppercase;margin-bottom:12px}.swarm .messagePreview:first-of-type{margin-top:8px}.count span{background:rgba(117,106,240,0.1);border-radius:50%;color:#756AF0;font-size:12px;padding:3px 7px}

